@import '../../styles/variables';
@mixin tablet {
    @media screen and (max-width: $page-width) { 
        padding: 50px 20px;
	}
	@media screen and (max-width: 1040px) {
		.flexbox {
			margin-right: 0px !important;
		}
	}
	@media screen and (max-width: $second-break-point) {
		.title-tech {
			ul {
				list-style-type: none;
				display: flex;
				margin: 0;
				padding: 0;
				flex-wrap: wrap;
				justify-content: center;
				
				li {
					margin-right: 2px !important;
					margin-top: 2px !important;
					border: 0px solid $main-color !important;
					padding: 2px !important;
					border-radius: 0px;
					transition: .5s all;
					min-width: 10px !important;
					text-align: center;
					// cursor: pointer;
					&:after{
						content: ',';
					}
					&:first-child {
						margin: 10px 0 0 0;
					}
					&:hover {
						background-color: $secondary-color;
						color: $main-font-color;
						border: 1px solid $secondary-color;
					}
					&:last-child::after {
						content: ''
					}
				}
			}
		}
	}
    @media screen and (max-width: $third-break-point) {
        .grid {
            .project {
                .screen {
                    width: 100%;
                }
            }
		}
		.title-tech {
			h3 {
				margin-top: 0;
				}
			ul {
				list-style-type: none;
				display: flex;
				margin: 0;
				padding: 0;
				flex-wrap: wrap;
				justify-content: flex-start;

				li {
					margin-right: 10px !important;
					margin-top: 10px !important;
					border: 1px solid $main-color !important;
					padding: 10px 25px !important;
					border-radius: 5px !important;
					transition: .5s all;
					min-width: 100px !important;
					text-align: center;
					// cursor: pointer;
					&:after{
						content: '';
					}
					&:first-child {
						// margin: 10px 0 0 0;
						margin: 10px 10px 0 0 !important;
					}
					&:hover {
						background-color: $secondary-color;
						color: $main-font-color;
						border: 1px solid $secondary-color;
					}
				}
			} 
		}
    }
    @media screen and (max-width: $fourth-break-point) {
        padding: 25px 10px;
        .grid {
            .project {
                .screen {
                    .details {
                        padding: 5px;
                            .title-tech {
                               h3 {
                                margin-bottom: 10px;
                                } 
                                ul {
                                    justify-content: space-between;
                                    li {
                                        margin-right: 0px !important;
                                        
                                    }
                                }
                            }
                            .buttons {
                                padding-top: 10px;
                                
                            }

                    }
                }
            }
        }
    }
}

.portfolio {
    max-width: $page-width;
    margin: 0 auto;
	padding: 50px 0;
	
    .title {
        border-left: 5px solid $secondary-color;
        h2 {
            padding: 0 0 0 10px;
            margin: 0;
            font-size: 2.4rem;
            text-transform: uppercase;
            }
        p {
            padding: 0 0 0 10px;
            margin: 0;
        }
    }
    .grid {
        .project {
            display: flex;
            margin: 50px 0 0 0;
            width: 100%;
            justify-content: space-between;
            flex-wrap: wrap;
            .screen {
                position: relative;
                overflow: hidden;
                width: 49%;
                margin-top: 20px;
                &:nth-of-type(1){
                    margin: 0;
                }
                &:nth-of-type(2){
                    margin: 0;
                }
                img {
                    max-width: 100%;
                    max-height: 100%;
                    display: block;
                    transition: 1s all;
                    &:hover {
                        transform: scale(1.3);
                        -webkit-filter: brightness(30%);
                        -webkit-transition: all 1s ease;
                        -moz-transition: all 1s ease;
                        -o-transition: all 1s ease;
                        -ms-transition: all 1s ease;
                        transition: all 1s ease;
                    }
                }
                .details {
                    position: absolute;
                    bottom: 0;
                    color: $main-color;
                    background-color: $semi-transparent;
                    width: 100%;
                    padding: 25px;
                    .title-tech {
                        
                        h3 {
                            margin-top: 0;
                            }
                        ul {
                            list-style-type: none;
                            display: flex;
                            margin: 0;
							padding: 0;
							flex-wrap: wrap;
							justify-content: flex-start;

                            li {
								margin-right: 10px;
								margin-top: 10px;
                                border: 1px solid $main-color;
                                padding: 10px 25px;
                                border-radius: 5px;
                                transition: .5s all;
                                min-width: 100px;
                                text-align: center;
                                // cursor: pointer;
                                &:first-child {
                                    margin: 10px 10px 0 0;
                                }
                                &:hover {
                                    background-color: $secondary-color;
                                    color: $main-font-color;
                                    border: 1px solid $secondary-color;
                                }
							}

                        } 
                    }
                    .buttons {
                        display: flex;
                        justify-content: space-between;
                        padding: 30px 0 0 0;
                        a {
                            color: $main-font-color;
                            text-decoration: none;
                            button {
                                background-color: $secondary-color;
                                border: 0px;
                                border-radius: 5px;
                                width: 100px;
                                padding: 10px 0;
                                cursor: pointer;
                                transition: .5s all;
                                &:hover {
                                    -webkit-box-shadow: 0px 0px 34px 0px $secondary-color-shadow;
                                    -moz-box-shadow: 0px 0px 34px 0px $secondary-color-shadow;
                                    box-shadow: 0px 0px 34px 0px $secondary-color-shadow;
                                }
                            }
                        }
                    }
                }
            }
        }
	}
	
	/* Tooltip container */
.tooltip {
	position: relative;
	display: inline-block;
	border-bottom: 1px dotted black; /* If you want dots under the hoverable text */
  }
  
  /* Tooltip text */
  .tooltip .tooltiptext {
	visibility: hidden;
	width: 120px;
	background-color: #555;
	color: #fff;
	text-align: center;
	padding: 5px 0;
	border-radius: 6px;
  
	/* Position the tooltip text */
	position: absolute;
	z-index: 1;
	bottom: 125%;
	left: 50%;
	margin-left: -60px;
  
	/* Fade in tooltip */
	opacity: 0;
	transition: opacity 0.3s;
  }
  
  /* Tooltip arrow */
  .tooltip .tooltiptext::after {
	content: "";
	position: absolute;
	top: 100%;
	left: 50%;
	margin-left: -5px;
	border-width: 5px;
	border-style: solid;
	border-color: #555 transparent transparent transparent;
  }
  
  /* Show the tooltip text when you mouse over the tooltip container */
  .tooltip:hover .tooltiptext {
	visibility: visible;
	opacity: 1;
  }

    @include tablet;
}